___CSS_LOADER_EXPORT___.push([module.id, `.tasks-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 1rem;
  padding: 2rem 5rem;
}

.active-list-header {
  align-self: flex-start;
  font-size: 2rem;
  font-weight: bold;
}

.add-task {
  min-width: 27rem;
  box-sizing: border-box;
  flex: 1;
  font-size: 1.2rem;
  padding: 0.8rem;
  padding-right: 3rem;
  background-color: #484848;
  border-radius: 0.3rem;
  border: none;
  color: #ffffff;
}

.add-task:focus-visible {
  outline: 2px solid #2563eb;
}

.add-task::placeholder {
  color: #c2c2c2;
}

.tasks {
  width: 100%;
  display: flex;
  max-height: 70vh;
  flex-direction: column;
  overflow-y: auto;
}

.add-task-bar {
  position: relative;
  display: flex;
  width: 100%;
}

.add-task-bar > img {
  position: absolute;
  right: 0.5rem;
  top: 0.3rem;
  height: 2rem;
  cursor: pointer;
  height: 2rem;
  border-radius: 0.5rem;
  padding: 0.2rem;
}

.add-task-bar > img:hover {
  background-color: #85858541;
}

.add-task-bar.disabled {
  pointer-events: none;
  opacity: 0.3;
}

.add-task-date {
  opacity: 0;
  position: absolute;
  right: 0.5rem;
  top: 0.3rem;
  border: none;
  margin: 0;
  padding: 0;
  height: 2.2rem;
  width: 2.2rem;
  border-radius: 0.5rem;
}

.add-task-date:focus-visible {
  outline: none;
}

.task {
  position: relative;
  flex: 0 1 auto;
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  border-radius: 0.5rem;
  transition: background 0.3s;
}

.task:hover {
  background: #484848;
}

.task.selected {
  background: #484848;
}

.task > .task-content {
  flex: 1;
  font-size: 1.1rem;
  background-color: transparent;
  padding-right: 8.5rem;
  color: #ffffff;
  border: none;
  outline: none;
}

.task > .task-checkbox {
  min-width: 1.5rem;
  height: 1.5rem;
  border-radius: 0.3rem;
  cursor: pointer;
}

.task-information {
  position: absolute;
  right: 3px;
  display: flex;
  flex: 1;
  align-items: center;
  gap: 1rem;
  padding-right: 0.5rem;
  cursor: context-menu;
  user-select: none;
}

.task-information > .task-date {
  color: #3c74f9;
}
