___CSS_LOADER_EXPORT___.push([module.id, `.task-details.active {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  border-left: 1px solid #3c3c3c;
  height: 100%;
}

.task-details > hr {
  width: 100%;
  margin: 0;
  padding: 0;
  color: #484848;
  opacity: 0.1;
}

.task-header > img {
  height: 1.7rem;
  width: 1.7rem;
  padding: 0.5rem;
  filter: invert(23%) sepia(71%) saturate(0%) hue-rotate(249deg)
    brightness(100%) contrast(10%);
  cursor: pointer;
  border-radius: 0.5rem;
}

.task-header > img:hover {
  background-color: #b4b4b432;
}

.task-header {
  display: flex;
  gap: 1rem;
}
.task-header > .task-content {
  flex: 1;
  font-size: 1.1rem;
  background-color: transparent;
  color: #ffffff;
  border: none;
  outline: none;
  font-weight: bold;
}

.task-date-status {
  display: flex;
  gap: 1rem;
  align-items: center;
  padding: 1rem 2rem;
}

.task-date-status > .task-checkbox {
  min-width: 1.5rem;
  height: 1.5rem;
  border-radius: 0.3rem;
  cursor: pointer;
}

.task-date-status > input[type="date"] {
  flex: 1;
  background-color: transparent;
  color: #3c74f9;
  border: none;
  outline: none;
  font-size: 1.2rem;
  font-weight: bold;
  cursor: pointer;
  border-radius: 0.5rem;
  padding: 0.5rem;
}

.task-date-status > input[type="date"]:hover {
  background-color: #85858541;
}

.task-description {
  flex: 1 1 10%;
  height: 100%;
  font-size: 1rem;
  background-color: transparent;
  color: #ffffff;
  border: none;
  resize: none;
}

.task-header-description {
  display: flex;
  flex: 1;
  flex-direction: column;
  gap: 1rem;
  padding: 1rem 2rem;
}

.task-description::placeholder {
  color: #c2c2c2;
}

.task-description:focus-visible {
  outline: none;
}

.task-checklist-container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  max-height: 50vh;
  overflow-y: auto;
}

.task-checklist-container > .checklist-item {
  display: flex;
  gap: 1rem;
  align-items: center;
  padding-bottom: 1rem;
  border-bottom: 0.0625rem solid #3c3c3c;
}

.task-checklist-container > .checklist-item:first-child {
  padding-top: 1rem;
  border-top: 0.0625rem solid #3c3c3c;
}

.task-checklist-container > .checklist-item:last-child {
  border-bottom: none;
  margin-bottom: 10%;
}

.checklist-content {
  flex: 1;
  font-size: 1rem;
  background-color: transparent;
  color: #ffffff;
  border: none;
  outline: none;
  padding: 0.2rem;
}

.checklist-checkbox {
  min-width: 1.2rem;
  height: 1.2rem;
  border-radius: 0.3rem;
  cursor: pointer;
}

.checklist-item > img {
  filter: invert(23%) sepia(71%) saturate(0%) hue-rotate(249deg)
    brightness(100%) contrast(10%);
  height: 1.5rem;
  width: 1.5rem;
  cursor: pointer;
  border-radius: 0.4rem;
  transition: background 0.3s;
  padding: 0.15rem;
}

.checklist-item > img:hover {
  background-color: #b4b4b432;
}

.task-actions {
  display: flex;
  align-items: center;
  gap: 1rem;
}

#list-selection {
  font-size: 1rem;
  font-weight: bold;
  background-color: #676767;
  border-radius: 0.5rem;
  padding: 0.5rem;
  cursor: pointer;
  transition: background 0.3s;
  margin-right: auto;
}

#list-selection:hover {
  background-color: #858585;
}

.task-actions > img {
  filter: invert(23%) sepia(71%) saturate(0%) hue-rotate(249deg)
    brightness(100%) contrast(10%);
  height: 2rem;
  width: 2rem;
  cursor: pointer;
  border-radius: 0.5rem;
  transition: background 0.3s;
  padding: 0.2rem;
}

.task-actions > img:hover {
  background-color: #b4b4b432;
}

.task-details.empty {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-left: 1px solid #3c3c3c;
}

.select-task-illustration {
  width: 10rem;
}

.select-task-caption {
  text-wrap: nowrap;
  text-align: center;
  font-size: 1rem;
  font-weight: bold;
  color: #858585;
}

.unselectable {
  pointer-events: none;
}
